import { Injectable } from '@angular/core';

import { MenuItem } from '../models/menu-item';

const SIDEBAR_ITEMS: MenuItem[] = [
    {name: 'trangchu', value: 'Trang Ch', linkRef: '/trang-chu'},
    {name: 'gioithieu', value: 'Gii Thiu', linkRef: '/gioi-thieu'},
    {name: 'dichvu', value: 'Dch V', linkRef: '/dich-vu'},
    //{name: 'khuyenmai', value: 'Khuyn Mi', linkRef: '/khuyen-mai'},
    {name: 'tintuc', value: 'Tin Tc', linkRef: '/tin-tuc'},
    {name: 'bando', value: 'Bn ', linkRef: '/ban-do'},
    {name: 'thanhtoan', value: 'Thanh Ton', linkRef: '/thanh-toan'},
    {name: 'lienhe', value: 'Lin H', linkRef: '/lien-he'}
];

@Injectable()
export class MenuService {
    
    constructor() {}

    getSidebarItems(): MenuItem[] {
        return SIDEBAR_ITEMS;
    }

    getMenuItemByName(name: string): MenuItem {
        let result = this.getSidebarItems().filter(item => {
            return (item.name === name);
        });
        return result? result[0] : null;
    }
}


